import Vue from 'vue'
import Router from 'vue-router'
import EventMonitorApp from './components/Apps/EventMonitorApp/EventMonitor.vue'
import DevicesApp from './components/Apps/DevicesApp/Devices.vue'
import DeviceCompose from './components/Apps/DevicesApp/DeviceCompose.vue'
import DeviceEdit from './components/Apps/DevicesApp/DeviceEdit.vue'
import Settings from './components/Apps/SettingsApp/SettingsMain.vue'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/eventmonitor',
            name: 'eventmonitor',
            component: EventMonitorApp
        },
        {
            path: '/devices',
            name: 'devices',
            component: DevicesApp
        },
        {
            path: '/devicecompose',
            name: 'devicecompose',
            component: DeviceCompose
        },
        {
            path: '/deviceedit',
            name: 'deviceedit',
            component: DeviceEdit
        },
        {
            path: '/settings',
            name: 'settings',
            component: Settings
        }],
    mode: 'history'
    })