<template>
    <b-card header="Card Header"
            header-text-variant="Dark"
            header-tag="header"
            header-bg-variant="light"
            footer-tag="footer"
            footer-bg-variant="light"
            style="height:90vh;">

        <template v-slot:header>
            <h5 class="m-0">Settings</h5>
        </template>

        <b-form id="formDevice" ref="formDevice">
            <b-row>
                <b-col sm="12">
                    <b-form-group id="input-group-1"
                                  label="Webhook"
                                  label-for="input-1"
                                  description="Please enter the webhook address to call on fingerprint events.">
                        <b-form-input id="input-1"
                                      type="text"
                                      required
                                      v-model="Settings.webhook"
                                      placeholder="Enter Webhook Address"></b-form-input>
                    </b-form-group>
                </b-col>
            </b-row>
        </b-form>
        <b-button slot="footer" form="formDevice" variant="success" class="btn float-right" v-if="!isEdtingSettings" v-on:click="SaveSettings()" :disabled="btnSaveEditDisabled">Save</b-button>
        <b-button slot="footer" form="formDevice" variant="success" class="btn float-right" v-if="isEdtingSettings" v-on:click="EditSettings()" :disabled="btnSaveEditDisabled">Edit</b-button>
    </b-card>
</template>
<script>
 import { RESTAPIMixin } from "../../../mixin/RESTAPIMixin"
    export default {
        mixins: [RESTAPIMixin],
        name: 'DeviceCompose',
        data: function () {
            return {
                Settings: { webhook: "", tenant: ''},
                btnSaveEditDisabled: true,
                isEdtingSettings: false,
                settingsId: 0
            }
        },
        mounted: function () {
                this.getSettings()
                .then(Result => {
                    if (Result.isSuccess) {
                        this.btnSaveEditDisabled = false;
                        if (Result.data[0].webhook == undefined) {
                            this.isEdtingSettings = false;

                        } else
                        {
                            this.isEdtingSettings = true;
                        }
                        this.Settings.webhook = Result.data[0].webhook;
                        this.settingsId = Result.data[0].id;
                        this.$toasted.show('Done', { theme: "outline", position: "bottom-right", duration: 1000, });
                    }
                    else {
                        this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000 });
                    }

                })
        },
        methods: {
            
            SaveSettings: function () {
                this.btnSaveEditDisabled = true;
                if (!this.$refs.formDevice.checkValidity())
                {
                    this.btnSaveEditDisabled = false;
                    this.$toasted.show('Please fill the required feilds', { theme: "outline", position: "bottom-right", duration: 1000, });
                    return;
                }
                this.btnSaveDisabled = true;
                this.$toasted.show('Saving ...', { theme: "outline", position: "bottom-right", duration: 1000, });
                this.saveSettings(this.Settings)
                    .then(Result => {
                        this.btnSaveDisabled = false;
                    if (Result.isSuccess) {
                        this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000, });
                        this.btnSaveDisabled = false;
                        this.btnEditDisabled = false;
                    }
                    else {
                        this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000 });
                        this.btnSaveDisabled = false;
                    }
                })
            },
            EditSettings: function () {
                this.btnSaveEditDisabled = true;
                if (!this.$refs.formDevice.checkValidity()) {
                    this.btnSaveEditDisabled = false;
                    this.$toasted.show('Please fill the required feilds', { theme: "outline", position: "bottom-right", duration: 1000, });
                    return;
                }
                
                this.$toasted.show('Saving ...', { theme: "outline", position: "bottom-right", duration: 1000, });
                this.editSettings(this.Settings, this.settingsId)
                    .then(Result => {
                        this.btnEditDisabled = false;
                        if (Result.isSuccess) {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000, });
                            this.btnEditDisabled = false;
                        }
                        else {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000 });
                            this.btnEditDisabled = false;
                        }
                        this.btnSaveEditDisabled = false;
                    })
            }
        }
    }
</script>

<style>
    b-card {

    }
</style>