<script>
    import Login from './components/Login.vue'

    export default {
        name: 'app',
        data: function () {
            return {
                ShowLogin: false,
                ShowApps: false,
                isCollapse: false,
                ShowAppNames: true,
                user: ""
            }
        },
        components: {
            Login
        },
        mounted: function () {
            this.title = "Ceylon Cloud Inventory";
            var userJSONstring = localStorage.getItem('OwnerInfo')//will turn off auto login
            if (userJSONstring != undefined) {
                var userJSONobject = JSON.parse(userJSONstring);
                if (userJSONobject.username != undefined || userJSONobject.username != "") {
                    this.user = userJSONobject.username;
                    this.ShowLogin = false;
                    this.ShowApps = true;
                    this.isCollapse = true;
                } else {
                    this.ShowLogin = true;
                }
            }
            else {
                this.ShowLogin = true;
            }
        },
        methods: {
            logout: function () {
                this.ShowLogin = true;
                this.ShowApps = false;
                localStorage.clear();
            },
            mouseover: function () {
                this.isCollapse = false;
                this.ShowAppNames = true;
            },
            mouseleave: function () {
                this.isCollapse = true;
                this.ShowAppNames = false;
            },
            appopen: function () {
                this.isCollapse = true;
                this.ShowAppNames = false;
            },
            loggedIn() {
                this.ShowLogin = false;
                this.ShowApps = true;
            },
            settings() {
                this.$router.push({ name: 'settings' });
            }
        }
    }
</script>
