<template>
    <b-card header="Card Header"
            header-text-variant="Dark"
            header-tag="header"
            header-bg-variant="light"
            footer-tag="footer"
            footer-bg-variant="light"
            style="height:90vh;">

        <template v-slot:header>
            <h5 slot="header" class="title m-0">Devices Edit<b-button variant="light" class="btn float-right" v-on:click="goback()"><font-awesome-icon icon="arrow-left" /></b-button></h5>
        </template>
        
        <b-form id="formDevice" ref="formDevice">
            <b-row>
                <b-col sm="6">
                    <b-form-group id="input-group-1"
                                  label="IP Address"
                                  label-for="input-1"
                                  description="IP address or domain name assigned to your fingerprint device.">
                        <b-form-input id="input-1"
                                      type="text"
                                      required
                                      v-model="Device.IP"
                                      placeholder="Enter IP Address"></b-form-input>
                    </b-form-group>
                </b-col>
                <b-col sm="6">
                    <b-form-group id="input-group-1"
                                  label="Device Name"
                                  label-for="input-1"
                                  description="Unic Name for your finger print device.">
                        <b-form-input id="input-1"
                                      type="text"
                                      required
                                      v-model="Device.deviceName"
                                      placeholder="Enter Device Name"></b-form-input>
                    </b-form-group>
                </b-col>
            </b-row>
            <b-row>
                <b-col sm="6">
                    <b-form-group id="input-group-1"
                                  label="Port"
                                  label-for="input-1"
                                  description="Enter port number of your fingerprint device.">
                        <b-form-input id="input-1"
                                      type="text"
                                      required
                                      v-model="Device.port"
                                      placeholder="Enter Port"></b-form-input>
                    </b-form-group>
                </b-col>
                <b-col sm="6">
                    <b-form-group id="input-group-1"
                                  label="Description"
                                  label-for="input-1"
                                  description="Enter a short discription.">
                        <b-form-input id="input-1"
                                      type="text"
                                      v-model="Device.description"
                                      placeholder="Enter Description"></b-form-input>
                    </b-form-group>
                </b-col>
            </b-row>
        </b-form>
        <b-button slot="footer" variant="success" class="btn float-right login_btn" v-on:click="EditDevice()" :disabled="btnEditDisabled">Edit</b-button>
    </b-card>
</template>
<script>
 import { RESTAPIMixin } from "../../../mixin/RESTAPIMixin"
    export default {
        mixins: [RESTAPIMixin],
        name: 'DeviceEdit',
        data: function () {
            return {
                Device: this.$route.params,
                btnEditDisabled: false
            }
        },
        methods: {
            EditDevice: function () {
                if (!this.$refs.formDevice.checkValidity()) {
                    this.$toasted.show('Please fill the required feilds', { theme: "outline", position: "bottom-right", duration: 1000, });
                    return;
                }

                this.$toasted.show('Saving ...', { theme: "outline", position: "bottom-right", duration: 1000, });
                this.editDevice(this.Device, this.Device.id)
                    .then(Result => {
                        if (Result.isSuccess) {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000, });
                            this.$router.push({ name: 'devices' });
                        }
                        else {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000 });
                            this.btnSaveDisabled = false;
                        }
                    })
            },
            goback: function () {
                this.$router.push({ name: 'devices' });
            }
        },        
    }
</script>

<style>
    b-card {

    }
</style>