<template>
    <div id="app">
        <transition>
            <div class="d-flex justify-content-center h-100">
                <div class="card">
                    <div class="card-header">
                        <h3>Sign In</h3>
                        <!--<div class="d-flex justify-content-end social_icon">
                            <span><i class="fab fa-facebook-square"></i></span>
                            <span><i class="fab fa-google-plus-square"></i></span>
                            <span><i class="fab fa-twitter-square"></i></span>
                        </div>-->
                    </div>
                    <div class="card-body">
                        <form id="LoginForm">
                            <div class="input-group form-group">
                                <div class="input-group-prepend">
                                    <span class="input-group-text"><font-awesome-icon icon="user-secret" /></span>
                                </div>
                                <input type="text" class="form-control" placeholder="username" v-model="user.username">

                            </div>
                            <div class="input-group form-group">
                                <div class="input-group-prepend">
                                    <span class="input-group-text"><font-awesome-icon icon="key" /></span>
                                </div>
                                <input type="password" class="form-control" placeholder="password" v-model="user.password">
                            </div>
                            <!--<div class="row align-items-center remember">
                            <input type="checkbox">Remember Me
                        </div>-->
                            <div class="form-group">
                                <b-button variant="success" class="btn float-right login_btn" v-on:click="UserLogin()" :disabled="btnLoginDisabled">Login</b-button>
                            </div>
                        </form>
                    </div>
                    <div class="card-footer">
                        <div class="d-flex justify-content-center links">
                            <p style="font-size:0.8vw;">Don't have an account? Please call us 0773121241</p>
                        </div>
                        <div class="d-flex justify-content-center">
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
    import { RESTAPIMixin } from "../mixin/RESTAPIMixin"
    export default {
        mixins: [RESTAPIMixin],
        name: 'Login',
        data: function () {
            return {
                user: { username: '', tenant: '', password: '' },
                btnLoginDisabled: false
            }
        },
        mounted: function () {
            localStorage.clear();
        },
        methods: {
            UserLogin: function () {
                this.btnLoginDisabled = true;
                this.$toasted.show('Waiting for authentication ...',{ theme: "outline", position: "bottom-right", duration: 1000, });
                this.getSecurityToken(this.user)
                    .then(Result => {
                        if (Result.isSuccess) {
                            localStorage.setItem('user', JSON.stringify({ user: Result.data.User, tenantId: Result.data.Tenant.TenantId, tenantName: Result.data.Tenant.TenantName, securityToken: Result.data.Key }));
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000, });
                            this.$parent.loggedIn();
                            this.$parent.user = Result.message.split(" ")[0];
                        }
                        else {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000 });
                            this.btnLoginDisabled = false;
                        }
                    });
            }
        }
    }
    
</script>

