<template>
    <b-card header="Card Header"
            header-text-variant="Dark"
            header-tag="header"
            header-bg-variant="light"
            footer-tag="footer"
            footer-bg-variant="light"
            style="height:90vh;">

        <template v-slot:header>
            <h5 class="m-0">Devices</h5>
        </template>

        <div class="row" align-v="center" style="height:410px">
            <b-button block v-if="devices.length == undefined" variant="info" disabled class="col-11.5 m-0" style="height:50px">
                <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
            </b-button>
            <div class="col-12">
                <div class="vertical-scrollbar scroll4">
                    <b-card style="padding:0" v-for="(device, index) in devices" :key='index' class=" mb-1 m-1 bg-dark view overlay zoom">
                        <b-row align-v="center">
                            <b-col sm="1">
                                <div :style="{backgroundColor: randomColor()}" class="number-circle">
                                    <p style="color: white;mix-blend-mode:lighten;text-align:center;margin-top:20px">{{index+1}}</p>
                                </div>
                            </b-col>
                            <b-col sm="2">
                                <p style="color: white;padding-top:10px;" class="text-truncate"> {{device.deviceName}}</p>
                            </b-col>
                            <b-col sm="2">
                                <p style="color: white;padding-top:10px;">{{device.IP}} : {{device.port}}</p>
                            </b-col>
                            <b-col sm="2">
                                <p style="color: white;padding-top:10px" v-if="device.IsActivated == true" class="text-light mt-15">Device is Active </p>
                                <p style="color: white;padding-top:10px" v-if="device.IsActivated == false" class="text-danger  mt-15">Device is Inactive</p>
                            </b-col>
                            <b-col sm="2">
                                <b-button variant="success" class="btn float-right login_btn btn-block" v-on:click="Acitvate(index)" v-if="!device.IsActivated">Activate</b-button>
                                <b-button variant="danger" class="btn float-right login_btn btn-block" v-on:click="Deactivate(index)" v-if="device.IsActivated">Deactivate</b-button>
                            </b-col>
                            <b-col sm="1">
                                <b-button variant="success" class="btn float-right login_btn btn-block" v-on:click="Edit(index)" v-if="!device.IsActivated">Edit</b-button>
                            </b-col>
                            <b-col sm="2">
                                <b-button slot="footer" variant="danger" class="btn float-right btn-circle-small" v-on:click="deleteDevice(index)" v-if="!device.IsActivated">
                                    <font-awesome-icon icon="times" />
                                </b-button>
                            </b-col>
                        </b-row>
                    </b-card>
                </div>
            </div>
        </div>
        <b-button slot="footer" variant="success" class="btn float-right login_btn btn-circle" v-on:click="AddDevice()" :disabled="btnReloadDisabled">
            <font-awesome-icon icon="plus" />
        </b-button>
    </b-card>
</template>
<script>
 import { RESTAPIMixin } from "../../../mixin/RESTAPIMixin"
    export default {
        mixins: [RESTAPIMixin],
        name: 'Devices',
        data: function () {
            return {
                devices: {},
                btnReloadDisabled: false
            }
        },
        mounted: function () {
            this.loadAllDevices();
        },
        methods: {
            loadAllDevices: function () {
                this.getDevices()
                    .then(Result => {
                        if (Result.isSuccess) {
                            this.devices = Result.data;
                            this.$toasted.show('Done', { theme: "outline", position: "bottom-right", duration: 1000, });
                        }
                        else {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000 });
                        }

                    })
            },
            AddDevice: function () {
                this.$router.push({ name: 'devicecompose' });
            },
            randomColor: function() {
                const r = () => Math.floor(256 * Math.random());

                return `rgb(${r()}, ${r()}, ${r()})`;
            },
            Acitvate: function (index) {
                this.setDeviceStatus(true, this.devices[index].id)
                    .then(Result => {
                        if (Result.isSuccess) {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000, });
                            this.loadAllDevices();
                        }
                        else {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000 });
                            this.btnSaveDisabled = false;
                        }
                    })
            },
            Deactivate: function (index) {
                this.setDeviceStatus(false, this.devices[index].id)
                    .then(Result => {
                        if (Result.isSuccess) {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000, });
                            this.loadAllDevices();
                        }
                        else {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000 });
                            this.btnSaveDisabled = false;
                        }
                    })
            },
            deleteDevice: function (index) {
                this.deleteSingleDevice(this.devices[index].id)
                    .then(Result => {
                        if (Result.isSuccess) {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000, });
                            this.$toasted.show("Updating device information...", { theme: "outline", position: "bottom-right", duration: 5000, });
                            this.loadAllDevices();
                        }
                        else {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-right", duration: 5000 });
                            this.btnSaveDisabled = false;
                        }
                    })
            },
            Edit: function (index) {
                this.$router.push({ name: 'deviceedit', params: this.devices[index] });
            }
        }
    }
</script>

<style scoped>
    @import "https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css";

    b-card {
    }

    .card-body {
        padding: 3px;
        overflow-x: hidden !important; /*for hiding horizontal scroll bar*/
        overflow-y: auto; /*for vertical scroll bar*/
        height: auto;
        scrollbar-color: rebeccapurple light;
        scrollbar-width: thin;
    }

    .btn-circle {
        width: 38px;
        height: 38px;
        padding: 1px;
        border-radius: 40px;
        font-size: 24px;
        line-height: 0;
        display: table-cell;
    }

    .number-circle {
        width: 50px;
        height: 50px;
        border-radius: 5px;
        font-size: calc(5px + 1.5vw);
        line-height: 10px;
        display: table-cell;
    }

    btn-circle-small {
        width: 50px;
        height: 50px;
        border-radius: 5px;
        font-size: 24px;
        line-height: 5px;
        display: table-cell;
    }

    .vertical-scrollbar {
        overflow-x: hidden !important; /*for hiding horizontal scroll bar*/
        overflow-y: auto; /*for vertical scroll bar*/
        height: auto;
        scrollbar-color: rebeccapurple light;
        scrollbar-width: thin;
    }

    .scroll4::-webkit-scrollbar {
        width: 5px;
        overflow-x: hidden !important;
    }

    .scroll4::-webkit-scrollbar-thumb {
        background: #666;
        border-radius: 20px;
    }

    .scroll4::-webkit-scrollbar-track {
        background: #ddd;
        border-radius: 20px;
    }
</style>