<template>
    <b-card header="Card Header"
            header-text-variant="Dark"
            header-tag="header"
            header-bg-variant="light"
            footer-tag="footer"
            footer-bg-variant="light"
            style="height:90vh;">

        <template v-slot:header>
            <h5 class="m-0">Event Monitor</h5>
        </template>

        <b-card-body>
            <div class="row" align-v="center" style="margin-bottom:10px;">
                <b-button block v-if="Events.length == undefined" variant="info" disabled class="float-righ col-11.5 m-4" style="height:50px">
                    <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                    <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                    <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                    <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                    <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                    <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                    <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                    <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                    <b-spinner Large type="grow"></b-spinner><b-spinner Large type="grow"></b-spinner>
                </b-button>
                <div class="col-12" style="margin-top:-35px;">
                    <div class="vertical-scrollbar scroll4" id="EventViewer" v-on:scroll="handleScroll($event)">

                        <b-card style="padding:0; height:35px" v-for="(event, index) in Events" :key='index' class=" mb-1 m-1 bg-dark view overlay zoom">

                            <b-row align-v="center">
                                <b-col lg="1">
                                    <div style="background-color:#38406e" class="number-circle">
                                        <p style="color: white;text-align:center;min-width:100px">{{event.id}}</p>
                                    </div>
                                </b-col>
                                <b-col lg="3">
                                    <p class="text-truncate" style="margin-top:-20px;color: white;"> {{event.event}}</p>
                                </b-col>
                                <b-col lg="8">
                                    <p class="text-truncate" style="margin-top:-20px;color: white;"> {{event.description}}</p>
                                </b-col>
                            </b-row>
                        </b-card>
                    </div>
                </div>
            </div>

        </b-card-body>


    </b-card>
</template>
<script>
    import { RESTAPIMixin } from "../../../mixin/RESTAPIMixin"
    export default {
        mixins: [RESTAPIMixin],
        name: 'Settings',
        data: function () {
            return {
                Events: [],
                pooling: null,
                fullhight: 0,
                scroller: 0
            }
        },
        mounted: function () {
            this.loaddata();
            this.pooling = setInterval(this.loaddata, 5000)
        },
        beforeDestroy() {
            clearInterval(this.pooling)
        },
        methods: {
            
            loaddata: function () {
                //this.$toasted.show('Loading...', { theme: "outline", position: "bottom-center" });
                this.getEvents(0,18)
                    .then(Result => {
                        //debugger
                        if (Result.isSuccess) {
                            this.Events = Result.data
                            //this.$toasted.show(Result.message, { theme: "outline", position: "bottom-center" });
                        }
                        else {
                            this.$toasted.show(Result.message, { theme: "outline", position: "bottom-center" });
                            this.btnLoginDisabled = false;
                        }
                        
                    });
            },
            handleScroll: function (e) {
                if (e.target.scrollTop === (e.target.scrollHeight - e.target.offsetHeight)) {
                    clearInterval(this.pooling);
                    var that = this;
                    this.getEventsFrom(0, 20, this.Events[this.Events.length - 1].id)
                        .then(Result => {
                            if (Result.isSuccess) {
                                //this.Events = Result.data
                                //that.Events.concat(Result.data);
                                for (var i = 0; i < Result.data.length; i++) {
                                    that.Events.push(Result.data[i])
                                }
                                //this.$toasted.show(Result.message, { theme: "outline", position: "bottom-center" });
                            }
                            else {
                                this.$toasted.show(Result.message, { theme: "outline", position: "bottom-center" });
                                this.btnLoginDisabled = false;
                            }

                        });
                }
                else if (e.target.scrollTop)
                {
                    clearInterval(this.pooling);
                    this.pooling = setInterval(this.loaddata, 5000);
                }
            }
        }
        
    }
</script>

<style scoped>
    @import "https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css";

    b-card {
        height: 350px;
    }

    .btn-circle {
        width: 50px;
        height: 50px;
        padding: 10px;
        border-radius: 40px;
        font-size: 24px;
        line-height: 10;
        display: table-cell;
    }

    .number-circle {
        width: 100px;
        height: 25px;
        border-radius: 5px;
        font-size: 14px;
        margin-top:-29px;
        margin-left:-15px;
        margin-top:-26px;
    }

    btn-circle-small {
        width: 50px;
        height: 50px;
        border-radius: 5px;
        font-size: 24px;
        line-height: 5px;
        display: table-cell;
    }

    .vertical-scrollbar {
        overflow-x: hidden !important; /*for hiding horizontal scroll bar*/
        overflow-y: auto; /*for vertical scroll bar*/
        height: 74vh;
        scrollbar-color: rebeccapurple light;
        scrollbar-width: thin;
        padding:-50px !important;
    }

    .scroll4::-webkit-scrollbar {
        width: 5px;
        overflow-x: hidden !important;
    }

    .scroll4::-webkit-scrollbar-thumb {
        background: #666;
        border-radius: 20px;
    }

    .scroll4::-webkit-scrollbar-track {
        background: #ddd;
        border-radius: 20px;
    }
</style>